<template>
  <div style="max-width: 100%;box-sizing: border-box">
    <myHeader title="财务中心"></myHeader>
    <div>
      <img src="../../assets/banner.jpg" alt="" class="banner">
      <div class="hint">
        猪只死亡新情况
        <span class="hint-close">x</span>
      </div>
      <div class="menu-grid">
        <div class="menu-item">
          <div class="menu-left">
            <img src="../../assets/menu-item/icon.png" class="menu-icon" alt="">
          </div>
          <div class="menu-content">
            <span class="title">领用记录</span>
            <span class="muted">辅助信息</span>
          </div>
        </div>
        <div class="menu-item">
          <div class="menu-left">
            <img src="../../assets/menu-item/icon.png" class="menu-icon" alt="">
          </div>
          <div class="menu-content">
            <span class="title">结算记录</span>
            <span class="muted">辅助信息</span>
          </div>
        </div>
        <div class="menu-item">
          <div class="menu-left">
            <img src="../../assets/menu-item/icon.png" class="menu-icon" alt="">
          </div>
          <div class="menu-content">
            <span class="title">批次记录</span>
            <span class="muted">辅助信息</span>
          </div>
        </div>
        <div class="menu-item">
          <div class="menu-left">
            <img src="../../assets/menu-item/icon.png" class="menu-icon" alt="">
          </div>
          <div class="menu-content">
            <span class="title">余额查询</span>
            <span class="muted">辅助信息</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import myHeader from '@/components/Header'
export default {
  name: 'finance',
  components: {
    myHeader
  }
}
</script>

<style scoped lang="scss">
  .hint{
    background: #FFEEE5;
    padding: 5px 5px 5px 10px;
    color:#F8731A;
    font-size: 14px;
    border-radius: 3px;
    text-decoration: #F8731A underline;
    box-sizing: border-box;
    width: calc(100vw - 16px);
    margin: 0 8px;
    position: relative;
    & .hint-close{
      position: absolute;
      right: 8px;
      top:5px;
    }
  }
  .banner{
    border-radius: 5px;
    box-shadow: 5px 5px 5px #E1E1E1;
    width: calc(100vw - 16px);
    box-sizing: border-box;
    margin: 8px;
  }
  .menu-grid{
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
    margin: 10px 0;
    & .menu-item{
      width: calc(50vw);
      display: flex;
      flex-flow: nowrap row;
      border-bottom: 1px solid #EBEBEB;
      box-sizing: border-box;
      padding: 5px 0 15px 20px;
      &:active{
        background-color: #E1E1E1;
      }
      &:nth-of-type(odd){
        border-right: 1px solid #EBEBEB;
      }
      & .menu-left{
        width: 45px;
      }
      & .menu-content{
        flex:1;
        display: flex;
        flex-flow: nowrap column;
        padding-left: 6px;
        & .title{
          font-size:18px;
          color:#1E2834;
        }
        & .muted{
          font-size:12px;
          color: #8A8E96;
        }
      }
      & .menu-icon{
        width:45px;
      }
    }
  }
</style>



